<template>
  <div>
    <p @click="dialogVisible = true"
       class="sub_header">
        
    </p>
    <el-dialog
      id="guarantee_dialog"
      center
      close-on-press-escape
      fullscreen
      :visible.sync="dialogVisible">
      <div id="guarantee_body">
        <img src="@/assets/img/home/black_pattern.svg" class="bg_pattern" alt="">
        <el-row id="guarantee_body_wrap">
          <el-col :xs="22" :sm="10" :md="7" :lg="6" :xl="5" id="guarantee_img_wrap">
            <img src="@/assets/img/info/guarantee.png" id="guarantee_img" alt="">
          </el-col>
          <el-col :xs="22" :sm="12" :md="11" :lg="10" :xl="9">
            <h2 id="guarantee_title">  </h2>
            <p class="guarantee_text">
                   ,        . 
                 .        , 
               ,  ,       ,     .
                 ,             
                .
                 We-Vibe, LELO, Ovo, Satisfyer, Mystim, Nexus, Womanizer, Svakom, Magic Motion, RestArt,
              Imtoy       1  .
                 Fun Factory   2   .
              <br> <br>
                      .        
                  .
            </p>
          </el-col>
        </el-row>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'Guarantee',
  data () {
    return {
      dialogVisible: false
    }
  }
}
</script>

<style scoped lang="scss">
  .sub_header {
    font-family: $primary-font;
    font-weight: 600;
    color: $color-info;
    font-size: 12px;
    line-height: 22px;
  }

  .sub_header:hover {
    cursor: pointer;
  }

  #guarantee_body {
    width: 100vw;
    height: 100vh;
  }

  #guarantee_body_wrap {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 10vh;
  }

  .bg_pattern {
    position: absolute;
    height: 100vh;
    width: 100vw;
    top: 0;
    object-fit: cover;
    opacity: 0.2;
    z-index: 0;
  }

  #guarantee_img_wrap {
    text-align: end;
  }

  #guarantee_img {
    height: 380px;
    margin: auto 0;
  }

  #guarantee_title {
    color: $color-primary;
    font-family: $secondary-font;
    font-size: 32px;
    letter-spacing: 2px;
    font-weight: 300;
    margin-top: 5px;
  }

  .guarantee_text {
    color: white;
    font-size: 12px;
    font-weight: 500;
    margin-top: 15px;
    padding-right: 20px;
  }

  @media only screen and (max-width: $xs-screen) {
    #guarantee_img_wrap {
      text-align: center;
    }
    #guarantee_body_wrap {
      margin-top: 1vh;
    }
  }
</style>



